import React from "react";
import { Outlet, Link } from "react-router-dom";
import logo from "../assets/logo-long.svg";
import { useTranslation } from "react-i18next";

// import mui
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Footer from "../components/Footer";
import LanguagePicker from "../components/LanguagePicker";
import CloseIcon from "@mui/icons-material/DisabledByDefault";
import MenuIcon from "@mui/icons-material/Menu";

// component
const Layout = () => {
  const { t } = useTranslation();
  const [menuDisplay, setMenuDisplay] = React.useState(false);

  //hande menu button
  const handleMenuButton = () => {
    setMenuDisplay(true);
  };

  //handlee close button
  const handleCloseButton = () => {
    setMenuDisplay(false);
  };

  // function
  return (
    <>
      <Box className="navbar sticky">
        <Container maxWidth="lg">
          <Grid
            container
            direction="row"
            alignItems="center"
            justifyContent="space-between"
          >
            <Box className="navbar_box">
              <Link className="navbar_link-icon" to="/">
                <img src={logo} className="App-logo" alt="logo" />
              </Link>
            </Box>
            <Box className="navbar_box_mini">
              <Grid
                container
                direction="row"
                justifyContent="space-between"
                alignItems="flex-start"
              >
                <Link className="navbar_link-icon" to="/">
                  <img src={logo} className="navbar_icon" alt="logo" />
                </Link>
                <span className="title">JAS-Vermittlung</span>
                {!menuDisplay ? (
                  <MenuIcon className="menu_icon" onClick={handleMenuButton} />
                ) : (
                  <CloseIcon
                    className="close_icon"
                    onClick={handleCloseButton}
                  />
                )}
              </Grid>
            </Box>
            <Box
              className={`navbar_link_container ${menuDisplay ? "" : "hide"}`}
            >
              <Link className="navbar_link" to="/about">
                {t("navbar.about")}
              </Link>

              {/* <Link className="navbar_link" to="/employer">
                Fr Arbeitgeber
              </Link> */}

              <Link className="navbar_link" to="/services">
                {t("navbar.services")}
              </Link>

              <Link className="navbar_link" to="/contact">
                {t("navbar.contact")}
              </Link>

              <LanguagePicker />
            </Box>
          </Grid>
        </Container>
      </Box>

      <Outlet />

      <Footer />
    </>
  );
};

export default Layout;
