import React from "react";
import { Paper, Box } from "@mui/material";
import ArrowCircleDownIcon from "@mui/icons-material/ArrowCircleDown";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";

const Banner = (props) => {
  const { t } = useTranslation();

  return (
    <div>
      <Paper variant="outlined" className="banner-container">
        <img src={props.src} alt={props.imageName} />
        <Box className="banner_title_container">
          <Box className="banner_title">
            <h2>{t(props.title)}</h2>
            <h3>{t(props.subtitle)}</h3>
          </Box>
        </Box>
        <Box className="banner_btn_container">
          <Link className="banner_btn_contact" to="/contact">
            {t("banner.contact")}
          </Link>
        </Box>
        <Box className="icon_arrow-down_container">
          <a href={"#" + props.nextContainerId}>
            <ArrowCircleDownIcon className="icon_arrow-down" />
          </a>
        </Box>
      </Paper>
    </div>
  );
};

export default Banner;
