import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import banner from "../assets/banner3.jpg";
import student3 from "../assets/student3.jpg";

// import mui
import Box from "@mui/material/Box";
import Banner from "../components/Banner";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";

// component
const Services = () => {
  const { t } = useTranslation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div>
      <Banner
        src={banner}
        imageName="Big field"
        title="banner.title"
        subtitle="banner.subtitle"
        nextContainerId="service"
      />
      <Box id="service" className="main_container">
        <Box className="page_title">
          <h1>JAS-Vermittlung</h1>
          <h3>{t("services.subtitle")}</h3>
        </Box>
        <Container maxWidth="lg">
          <Grid container spacing={2}>
            <Grid item md={6}>
              <Box className="list_title">
                <h2>{t("services.list.title")}</h2>
              </Box>
              <div className="list_container">
                <ul>
                  <li>{t("services.list.text1")}</li>
                  <li>{t("services.list.text2")}</li>
                  <li>{t("services.list.text3")}</li>
                  <li>{t("services.list.text4")}</li>
                  <li>{t("services.list.text5")}</li>
                  <li>{t("services.list.text6")}</li>
                  <li>{t("services.list.text7")}</li>
                </ul>
              </div>
            </Grid>
            <Grid item md={6}>
              <Box className="img_container">
                <img src={student3} alt="student" />
              </Box>
            </Grid>
          </Grid>
          <Box className="btn_container">
            <Link className="btn_contact" to="/contact">
              {t("banner.contact")}
            </Link>
          </Box>
        </Container>
      </Box>
    </div>
  );
};

export default Services;
