import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import banner from "../assets/banner1_1.jpg";
import student1 from "../assets/student1.jpg";
import student2 from "../assets/student2.jpg";
import student3 from "../assets/student3.jpg";
import student4 from "../assets/student21.jpg";
import student5 from "../assets/student5.jpg";
import maid from "../assets/hotelmaid1.jpg";
import waiter from "../assets/waiter1.jpg";

// import mui
import Box from "@mui/material/Box";
import Banner from "../components/Banner";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";

// component
const Home = () => {
  const { t } = useTranslation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div>
      <Box>
        <Banner
          src={banner}
          imageName="Big field"
          title="banner.title"
          subtitle="banner.subtitle"
          nextContainerId="main"
        />
      </Box>
      <Box id="main" className="main_container">
        <Container maxWidth="lg">
          <Box className="page_title">
            <h1>JAS-Vermittlung</h1>
            <h3>{t("about.subtitle")}</h3>
            <div className="text_container">{t("home.text1")}</div>
          </Box>
        </Container>
        <Container maxWidth="lg">
          <Box className="section_container">
            <Box className="title">
              <h1>Unsere Bereiche</h1>
            </Box>
            <Grid container spacing={2}>
              <Grid item md={4}>
                <Card>
                  <img
                    src={student4}
                    alt="student"
                    style={{ height: "auto", width: "100%" }}
                  />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                      Landwirtschaft
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      Die Landwirtschaft ist ein Wirtschaftszweig, der auf
                      landwirtschaftlichen Flchen landwirtschaftliche oder
                      tierische Produkte herstellt.
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Link className="card_link" to="/farming">
                      Mehr erfahren
                    </Link>
                  </CardActions>
                </Card>
              </Grid>
              <Grid item md={4}>
                <Card>
                  <img
                    src={maid}
                    alt="student"
                    style={{ height: "auto", width: "100%" }}
                  />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                      Hotel
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      Eine kommerziell gefhrte Einrichtung, die Unterkunft und
                      in der Regel auch Mahlzeiten fr Gste bietet und hufig
                      ber eine ffentliche Bar verfgt.
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Link className="card_link" to="/hotel">
                      Mehr erfahren
                    </Link>
                  </CardActions>
                </Card>
              </Grid>
              <Grid item md={4}>
                <Card>
                  <img
                    src={waiter}
                    alt="student"
                    style={{ height: "auto", width: "100%" }}
                  />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                      Restaurant
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      Ein Restaurant ist ein Unternehmen, das Speisen zubereitet
                      und serviert zu Kunden. Mahlzeiten werden in der Regel
                      serviert und vor Ort gegessen.
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Link className="card_link" to="/restaurant">
                      Mehr erfahren
                    </Link>
                  </CardActions>
                </Card>
              </Grid>
            </Grid>
          </Box>
        </Container>
        <Container maxWidth="lg">
          <Box className="section_container">
            <Box className="title">
              <h1>{t("home.advantages.title")}</h1>
            </Box>
            <Grid container spacing={2}>
              <Grid item md={6}>
                <div className="list_container">
                  <ul>
                    <li>{t("home.advantages.text1")}</li>
                    <li>{t("home.advantages.text2")}</li>
                    <li>{t("home.advantages.text3")}</li>
                    <li>{t("home.advantages.text4")}</li>
                    <li>{t("home.advantages.text5")}</li>
                    <li>{t("home.advantages.text6")}</li>
                  </ul>
                </div>
              </Grid>
              <Grid item md={6}>
                <Box className="img_container">
                  <img src={student3} alt="student" />
                </Box>
              </Grid>
            </Grid>
          </Box>
          <Box className="section_container">
            <Box className="title">
              <h1>{t("home.requirement.title")}</h1>
            </Box>
            <Grid container spacing={2}>
              <Grid item md={6}>
                <div className="list_container">
                  <ul>
                    <li>{t("home.requirement.text1")}</li>
                    <li>{t("home.requirement.text2")}</li>
                    <li>{t("home.requirement.text3")}</li>
                    <li>{t("home.requirement.text4")}</li>
                    <li>{t("home.requirement.text5")}</li>
                    <li>{t("home.requirement.text6")}</li>
                  </ul>
                </div>
              </Grid>
              <Grid item md={6}>
                <Box className="img_container">
                  <img src={student5} alt="student" />
                </Box>
              </Grid>
            </Grid>
          </Box>

          <Box className="btn_container">
            <Link className="btn_contact" to="/contact">
              {t("banner.contact")}
            </Link>
          </Box>
        </Container>
      </Box>
    </div>
  );
};

export default Home;
